import React, { Component } from "react";
import { observer } from "mobx-react";
import "./Aboutme.css";
import AboutmeState from "./AboutmeState";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";

//=======================================================
// Props that about me page is receiving.
//=======================================================
type AboutmeProps = {
  state: AboutmeState;
};

//=======================================================
// Aboutme view.
//=======================================================
@observer
export default class Aboutme extends Component<AboutmeProps> {
  scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  };

  render() {
    return (
      <div className="Aboutme">
        <div className="Aboutme-box">
          <Card className="text-center">
            <Card.Header as="h5">
              My Journey as a Self-Taught Software Engineer
            </Card.Header>
            <Card.Body>
              <Card.Title>Introduction</Card.Title>
              <Card.Text>
                I’m a full-time software engineer with 2 years of working
                experience in the tech industry.
              </Card.Text>
              <Card.Text>
                Before getting here, I was a graphic designer in the media
                industry.
              </Card.Text>
              <Card.Text>
                To be honest, it wasn't easy to switch career with only a
                diploma in computer engineering.
              </Card.Text>
              <Card.Text>
                Many firms are seeking for fresh graduate or experienced
                candidate with an undergraduate degree, as a self-learner, I
                have to work even harder to get a chance.
              </Card.Text>
              <Card.Text>
                Learning every day is something that grabs a lot of benefits for
                me.
              </Card.Text>
              <Card.Text>
                By giving my best everyday, the day came where I landed my first
                developer job.
              </Card.Text>
              <Card.Text>
                I have learned a lot from work since then, and the sense of
                achievement kept me motivated to strive even harder.
              </Card.Text>
              <Card.Text>It was tough, but it was worth it.</Card.Text>
              <Card.Text>In a nutshell, never give up!</Card.Text>
              <Card.Text>Thanks for reading!</Card.Text>
            </Card.Body>
          </Card>

          <Button
            className="backtotop-button"
            variant="outline-secondary"
            onClick={this.scrollToTop}
          >
            ↑ Back to Top
          </Button>
        </div>
      </div>
    );
  }
}
